import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { DetailsVoucherComponent } from './details-voucher/details-voucher.component';

const voucherRoutes: Routes = [
	{ path: 'details-voucher/:id', component: DetailsVoucherComponent }
];

@NgModule({
    imports:[RouterModule.forChild(voucherRoutes)],
    exports: [RouterModule]
})
export class VoucherRoutingModule{}



