import { Component, OnInit, OnDestroy } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { UserService, UserActivityService } from 'app/common/user';
import { Router, ActivatedRoute } from '@angular/router';
import { VoucherService } from 'app/common/voucher/voucher.service';
import { MediaService } from 'app/common/media/media.service';
import { DomSanitizer } from '@angular/platform-browser';
import { NavigationEnd } from '@angular/router';


@Component({
  selector: 'app-details-voucher',
  templateUrl: './details-voucher.component.html',
  styleUrls: ['./details-voucher.component.css']
})
export class DetailsVoucherComponent implements OnInit, OnDestroy {
  private pick;
  private voucherCode;
  private voucher;
  private selected = 1;
  private selectedCategory;
  private quantity = [];
  vouchers: any = [];
  images: any[] = [];
  categories: any[] = [];
  image: any;
  private category = null;
  alive = true;
  currentPage = 0;
  totalProducts = 0;



  constructor(
    private userService: UserService,
    private router: Router,
    private route: ActivatedRoute,
    private mediaService: MediaService,
    private sanitizer: DomSanitizer,
    private voucherService: VoucherService,
    private useractivityService: UserActivityService
  ) { }

  ngOnInit() {
    for (let i = 0; i < 5; i++) {
      this.quantity.push({ value: i + 1 });
    }

    this.route.params.subscribe((mapParams) => {
      this.voucherCode = mapParams["id"];
      this.loadDetails();
    });
    this.getVouchersRecommends();
    this.router.routeReuseStrategy.shouldReuseRoute = function () {
      return false;
    };

    this.router.events.subscribe((evt) => {
      if (evt instanceof NavigationEnd) {
        this.router.navigated = false;
        window.scrollTo(0, 0);
      }
    });
    this.getCategories();
  }

  getCategories() {
    this.voucherService.getCategories().subscribe((r) => {
      this.categories = r;
    })
  }

  changeCategory(category: any) {
    this.selectedCategory = category;
    this.category = category.categoryCode;
    this.getVouchersRecommends();
    
  }
  setManageCart() {
    this.useractivityService.manageCart(this.voucherCode, this.selected).takeWhile(() => this.alive).subscribe(
      result => {
        console.log(result);
      },
      error => { console.log('Error occured', error); });
  }

  gotoDispensarie(dispensarie: any) {
    this.router.navigate(['/dispensaries/view-dispensaries', dispensarie]);
  }
  loadDetails() {
    this.voucherService.getVoucherDetail(this.voucherCode).takeWhile(() => this.alive).subscribe((d) => {
      this.voucher = d;
      this.getImages();
    })
  }

  getImages() {
    if (!this.voucher || !this.voucher.media) {
      return;
    }
    this.images = [];
    for (const img of this.voucher.media) {
      if (!img.mediaUrl) {
        continue;
      }
      this.mediaService.getMedia(img.mediaUrl).takeWhile(() => this.alive).subscribe(
        result => {
          let rsimg = this.sanitizer.bypassSecurityTrustResourceUrl(result)
          if (!this.image) {
            this.image = rsimg;
          }
          this.images.push(rsimg);
        }
      );
    }

  }

  getVouchersRecommends() {
    this.vouchers = [];
    this.voucherService.getVouchers(this.category, '4', this.currentPage, 10, 1).subscribe(
      res => {

        for (var i in res) {
          this.voucherService.getVoucherDetail(res[i].voucherVendorLocationCode).takeWhile(() => this.alive).subscribe(
            res => {
              this.vouchers.push(res);
              if (this.vouchers && this.vouchers[0]) {
                this.totalProducts = this.vouchers[0].totalCount;
              }
            },
            err => {
              console.log('Error occured');
            });
        }
        console.log(this.vouchers);
      },
      err => {
        console.log('Error occured');
      });

  }
  changeImage(img) {
    this.image = img;
  }

  lookupAsync = (query: string): Observable<any[]> => {
    if (!query) {
      return null;
    }
    return this.userService.search(query).map(response => response.vouchers);
  }
  changed() {
    this.gotoProduct(this.pick.id);
  }
  async gotoProduct(code: any) {
    await this.router.navigate(["/voucher/details-voucher/", code]);
    setTimeout(() => { this.pick = null; }, 20)

  }

  ngOnDestroy(): void {
    this.alive = false;
  }

}



