import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { VoucherRoutingModule } from './voucher-routing.module';
import { DetailsVoucherComponent } from './details-voucher/details-voucher.component';
import { CardModule } from '../../common/components/card';
import { FlexLayoutModule } from '@angular/flex-layout';
import { VoucherModule as voucherM } from '../../common/voucher';
import { MatSelectModule } from '@angular/material/select';
import { VoucherCardModule } from '../../common/components/voucher';

import {
  AgmCoreModule
} from '@agm/core';
import { NglModule } from 'ng-lightning/ng-lightning';

@NgModule({
  imports: [
    CommonModule,
    FlexLayoutModule,
    VoucherRoutingModule,
    AgmCoreModule,
    CardModule,
    voucherM,
    MatSelectModule,
    VoucherCardModule,
    NglModule
  ],
  declarations: [DetailsVoucherComponent]
})
export class VoucherModule { }



